import { Injectable } from '@angular/core';
import { Vendor } from './add-model';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AddEditVendorService {

  vendor:Vendor;
  constructor(private httpService:HttpClient) { }

  getAllVendors():Observable<Vendor[]>{

    return this.httpService.get<Vendor[]>(environment.APIUrl+'/vendor/null')
  }

  insertVendor(vendor: Vendor) {
    let body = JSON.stringify(vendor);
    console.log(vendor);
    let headers = new HttpHeaders({ 'Content-Type': 'application/json' });
    let options = { headers: headers }
    console.log('before save');
    // return this._httpService.post(environment.APIUrl+'insertstaff/'+formData.roleName ,body, options);

    if (vendor.vendor_id!=null) {
      console.log(vendor.vendor_id)
      return this.httpService.put(environment.APIUrl + '/updatevendor', body, options);
    } else {
      return this.httpService.post(environment.APIUrl + '/insertvendor', body, options);
    }
  }

  getVendorbyId(vId: any): Observable<Vendor> {
    return this.httpService.get<Vendor>(environment.APIUrl + '/vendors/' + vId)
  }
}
