import { Component, OnInit } from '@angular/core';
import { ViewVendorService } from './view-vendor.service';
import { Router } from '@angular/router';
import { View } from './view-model';
import { Vendor } from '../add-edit-vendor/add-model';
import { AuthService } from '../guards/auth.service';

@Component({
  selector: 'app-view-vendor',
  templateUrl: './view-vendor.component.html',
  styleUrls: ['./view-vendor.component.scss']
})
export class ViewVendorComponent implements OnInit {

  vendors: View[];
  vendor = new View();
  searchId: any;
  // vendor = new Vendor();

  constructor(private auth:AuthService,public _venderService: ViewVendorService, private router: Router) { }

  ngOnInit() {
    this.getAllVendors();
  }
  edit_Vendor(vendor_id:any){
    this.router.navigate(['add/',+vendor_id]);
    
  }

  getAllVendors(): void {
    this._venderService.getVendor()
      .subscribe((venderData) => {
        this.vendors = venderData
      }, (error) => {
        console.log(error);
      });

  }
  addVendor() {
    this.router.navigate(['add'])
  }

  //selecting  id for disable
  onSelect(selectedVendor: any) {
    console.log("selected Item Id: ", selectedVendor.Id);
    this.searchId = selectedVendor.vendor_id;
  }

  //disable medicine
  disableVendor() {

    this._venderService.disableVendors(this.searchId)
      .subscribe((vendorsData) => {
        console.log(vendorsData);
        this.getAllVendors();

      }, (error) => {
        console.log(error);
      });
  }

  viewContact(id:any){
    this.router.navigate(['contact',id]);
  }
  back(){
    this.router.navigate(['view']);
  }

  logout(){
    this.auth.logout();
  }

  
}
