import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { View } from './view-model';
import { environment } from 'src/environments/environment';
import { Vendor } from '../add-edit-vendor/add-model';

@Injectable({
  providedIn: 'root'
})
export class ViewVendorService {
  vendor = new Vendor();
  constructor(private _httpService: HttpClient,private router:Router) { }

  getVendor(): Observable<View[]>{
    return this._httpService.get<View[]>(environment.APIUrl+'/vendor/null')
  }

  disableVendors(searchId: any): any{
    let body = JSON.stringify(searchId);
    let headers = new HttpHeaders({'Content-Type':'application/json'});
    let options = { headers: headers }

      return this._httpService.put<View[]>(environment.APIUrl+'/disablevendor/'+searchId, body, options);



  }
  getVendorbyId(vId:any): Observable<Vendor> {
    return this._httpService.get<Vendor>(environment.APIUrl + '/vendors/' + vId)
  }

}
