import { Component, OnInit } from '@angular/core';
import { ViewVendorService } from '../view-vendor/view-vendor.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Vendor } from '../add-edit-vendor/add-model';
import { View } from '../view-vendor/view-model';
import { AuthService } from '../guards/auth.service';

@Component({
  selector: 'app-view-contact',
  templateUrl: './view-contact.component.html',
  styleUrls: ['./view-contact.component.scss']
})
export class ViewContactComponent implements OnInit {

  constructor(private auth:AuthService,private _service:ViewVendorService,private route:ActivatedRoute,private router:Router) { }

  ngOnInit() {
    this.getVendorById();
    this.resetform();
  }

  vId:any;
  vendor = new View();

  back(){
    this.router.navigate(['view'])
  }
  logout(){
    this.auth.logout();
  }

  resetform() {
    this._service.vendor = {
      vendor_id: null,
      vendor_name: '',
      address: '',
      location: '',
      service: '',
      pincode: '',
      contact_id: '',
      name: '',
      dept: '',
      email: '',
      phone: '',
      isActive: null
    }
  }

  getVendorById(){
    this.resetform();
    this.vId=this.route.snapshot.paramMap.get('id');
    this._service.getVendorbyId(this.vId)
    .subscribe((data) =>{
      this.vendor=data;
      console.log('inside');
      console.log(data);
      
      console.log(this.vendor.vendor_name);
    },(error)=>{
      console.log(error);
    })
  }
  
  
}
