import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ViewVendorComponent } from './view-vendor/view-vendor.component';
import { LoginComponent } from './login/login.component';
import { AddEditVendorComponent } from './add-edit-vendor/add-edit-vendor.component';
import { ViewContactComponent } from './view-contact/view-contact.component';
import { AuthGuard } from './guards/auth.guard';


const routes: Routes = [
  
  {
    path:'',
    redirectTo: 'login', pathMatch: 'full'
  },
  {
    path:'login',
    component:LoginComponent

  },

  {
 
    path:'view',
    component:ViewVendorComponent, canActivate: [AuthGuard]

  },
  {
    path:'add/:vendor_id',
    component:AddEditVendorComponent, canActivate: [AuthGuard]

  },
  {

    path:'add',
    component:AddEditVendorComponent, canActivate: [AuthGuard]

  },
  {

    path:'contact/:id',
    component:ViewContactComponent, canActivate: [AuthGuard]

  },
  {

    path:'contact',
    component:ViewContactComponent, canActivate: [AuthGuard]

  }

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
